.about-us {
  @include page-grid;

  &__title {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 1 / 4;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__content {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 4 / 7;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__description {
    margin: 0;
    font-family: 'Space Mono', monospace;
    font-weight: 400;
    line-height: 22.4px;

    @include on-desktop {
      margin-left: 48px;
    }
  }
}
